import styled from 'styled-components'

const margin = "7.5rem";


export const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0px;
  padding: 0px;
  width: 100vw;
  background-image: url(/static/images/home/pope.png);
  min-height: 85vh;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 100vw auto;
  .content {
    margin-left: ${margin}
  }

  .days {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &__closing {
    height: 100%;
      width: 30%;
      background-color: gray;
      color: white;
      padding: 10px 0px;
    }
    &__days {
      padding: 10px 0px;
      flex: auto;
      color: #4d4d4d;
      font-size: 32px;
      font-weight: 200;
      padding-left: 20px;
    }
  }
`;



